.page-wrapper {
	overflow   : hidden;
}

.wrapper {
	margin             : auto;
	width              : (1038 + 60) * 1px;
	padding            : 0 20px;
	max-width          : 100%;
	-webkit-box-sizing : border-box;
	-moz-box-sizing    : border-box;
	box-sizing         : border-box;
	@media screen and (min-width : $break-table) {
		padding : 0 30px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}
